import { Component, OnInit, ViewChild } from '@angular/core';
import { DataTableDirective } from 'angular-datatables';
import { Subject } from 'rxjs';
import Swal from 'sweetalert2';
import { idUsuarioTemp, isCaracter, isNumber, isTel } from '../../../../constantes/constantesproyecto'
import { Categoria, Equipo, Ubicacion, Usuario } from '../../models/equipo';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { EquipoService } from '../../service/equipo.service';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { AutenticacionService } from 'src/app/account/auth/login/service/autenticacion.service';
import { Role } from 'src/app/enum/role.enum';
// import { Usuario } from 'src/app/account/auth/login/models/usuario';

@Component({
  selector: 'app-listaequipo',
  templateUrl: './listaequipo.component.html',
  styleUrls: ['./listaequipo.component.scss']
})
export class ListaequipoComponent implements OnInit {

  breadCrumbItems: Array<{}>;
  selectedYear: number; // Variable para almacenar el año seleccionado
  years: number[] = [];
  equipos: Equipo[] = [];
  equipo!: Equipo//almacenar guardar
  // categoria !: Categoria[];
  categorial: Categoria[] = [];//LISTA MOSTRAR
  ubicacionel: Ubicacion[] = [];//LISTA MOSTRAR
  // ubicacion !: Ubicacion[];
  // usuario !: Usuario[];
  showLoading = false;
  user!: Usuario
  estado: string;
  /**ROLES */
  estudiante = Role.ROLE_ESTUDIANTE;

  formulario!: FormGroup;
  equipoE: Equipo;//para editar estado

  /**DataTables */
  @ViewChild(DataTableDirective, { static: false }) dtElement: DataTableDirective
  dtOptions: any = {};
  dtTrigger: Subject<Equipo> = new Subject<Equipo>();
  constructor(
    private equipoService: EquipoService,
    private modalService: NgbModal,//para abrir el modal openModal()
    private fb: FormBuilder,
    private authService: AutenticacionService
  ) { this.formulario = this.inifc() }

  ngOnInit(): void {
    this.breadCrumbItems = [{ label: 'Equipo' }, { label: 'Registro', active: true }];
    this.user = this.authService.getUserFromLocalCache();
    /**table */
    this.dtOptions = {
      columnDefs: [
        { className: "dt-center", targets: "_all" },
        { orderable: false, targets: [1] },
        { searchable: false, targets: [0, 8] },
        // { width: "50%", targets: [0, 1] },
        { responsivePriority: 1, targets: 2 },
        { responsivePriority: 1, targets: -1 },
      ],
      lengthMenu: [5, 10, 20, 50],
      destroy: true,
      language: {
        url: "//cdn.datatables.net/plug-ins/1.13.5/i18n/es-ES.json",
        lengthMenu: "Mostrar _MENU_ registros",
        zeroRecords: "Ningún dato encontrado",
      },
      pagingType: "full_numbers",
      responsive: true,
    };
    const currentYear = new Date().getFullYear();
    for (let year = currentYear; year >= currentYear - 20; year--) {
      this.years.push(year);
    }



    this.cargarDatos();

    this.cargarCategorias();
    this.cargarUbicaciones();

  }
  private inifc(): FormGroup {
    return this.fb.group({

      numserie: ['', [Validators.required]],
      modelo: ['',],
      numinventario: ['',],
      marca: ['',],
      anio: ['',],
      estado: ['', [Validators.required]],
      categoria: ['', [Validators.required]],
      ubicacione: ['', [Validators.required]],
      ip: ['',],
      descripcion: ['',],


    });
  }
  // getPermisos() {
  //   return this.authService.hasRole(this.estudiante)
  // }
  reset() {
    this.formulario.reset()

  }
  cargarDatos() {
    this.equipoService.equipos().subscribe((resp: any) => {
      this.equipos = resp;
      this.dtTrigger.next(null);

    });
  }



  cargarCategorias() {
    this.equipoService.categorias().subscribe((resp: any) => {
      this.categorial = resp;

    })
  }

  cargarUbicaciones() {
    this.equipoService.ubicaciones().subscribe((resp: any) => {
      this.ubicacionel = resp;
    })
  }

  guardar() {
    this.showLoading = true
    if (this.formulario.valid) {
      const numserie = this.formulario.get("numserie").value;
      const numinventario = this.formulario.get("numinventario").value;
      const c: Categoria = new Categoria(this.formulario.get("categoria").value);
      const modelo = this.formulario.get("modelo").value;
      const marca = this.formulario.get("marca").value;
      const estado = this.formulario.get("estado").value;
      const ip = this.formulario.get("ip").value;
      const anio = this.formulario.get("anio").value;
      const u: Ubicacion = new Ubicacion(this.formulario.get("ubicacione").value);
      const descripcion = this.formulario.get("descripcion").value;
      const usuario = new Usuario(this.user.id);
      this.equipo = new Equipo(numinventario, numserie, marca, modelo, anio, descripcion, ip, estado, c, u, usuario);
      if (this.existe(this.equipo.numserie.toLowerCase() || this.equipo.numserie.toUpperCase())) {
        Swal.fire({
          confirmButtonColor: "#a90000",
          title: "Error",
          text: "Ya existe el número de serie",
          icon: "error",
        });

      } else {
        this.equipoService.guardar(this.equipo).subscribe((resp: any) => {
          if (resp) {
            this.showLoading = false
            Swal.fire({
              confirmButtonColor: "#a90000",
              title: "Éxito",
              text: `Almacenado correctamente`,
              icon: "success",
            });
            this.formulario.reset();
            this.modalService.dismissAll();
            this.reload();
          } else {
            Swal.fire({
              confirmButtonColor: "#a90000",
              title: "Advertencia",
              text: `Ocurrio un problema`,
              icon: "warning",
            });
          }
        });
      }
    } else {
      Swal.fire({
        confirmButtonColor: "#a90000",
        position: "center",
        title: "Faltan datos en el formulario",
        text: "completar campos requeridos",
        icon: "warning",
      });
    }
  }

  existe(numserie: string): boolean {
    const nombreEnMinusculas = numserie.toLowerCase();
    return this.equipos.some((equipo) => {
      return equipo.numserie.toLowerCase() === nombreEnMinusculas;
    });
  }





  openModalEditar(content: any, equipoe: Equipo) {
    this.equipoE = equipoe;
    this.formulario.patchValue(equipoe);
    this.modalService.open(content, { size: 'lg', backdrop: 'static', keyboard: false });
    this.formulario.get("categoria").setValue(equipoe.categoria.id);
    this.formulario.get("ubicacione").setValue(equipoe.ubicacione.id);
  }

  openModalEditarEstado(content: any, equipoe: Equipo) {
    this.equipoE = equipoe;
    this.formulario.patchValue(equipoe);
    // this.modalService.open(content);
    this.modalService.open(content, { backdrop: 'static', keyboard: false, size: 'sm' });

  }


  editar() {
    this.showLoading = false
    const anioCompra = this.formulario.get('anio').value;
    const anioActual = new Date().getFullYear();
    const aniosTranscurridos = anioActual - anioCompra;
    const nuevoEstado = this.formulario.controls['estado'].value;

    if (nuevoEstado === 'Bueno' || nuevoEstado === 'Dañado') {

      if (this.formulario.valid) {
        const numSerie = this.formulario.controls['numserie'].value
        if (numSerie === this.equipoE.numserie) {
          this.realizarModificacion();
        } else {
          if (this.existe(numSerie.toLowerCase() || numSerie.toUpperCase())) {
            Swal.fire({
              confirmButtonColor: "#a90000",
              title: "Error",
              text: "Ya existe el número de serie",
              icon: "error",
            });
          } else {
            
            this.realizarModificacion();
          }
        }
      } else {
        Swal.fire({
          confirmButtonColor: "#a90000",
          position: 'center',
          title: 'Faltan datos en el formulario',
          text: 'completar campos requeridos',
          icon: 'warning',
        });
      }
    } else if (aniosTranscurridos >= 4) {
      if (this.formulario.valid) {
        this.equipoE.id = this.equipoE.id
        this.equipoE.numinventario = this.formulario.controls['numinventario'].value
        this.equipoE.numserie = this.formulario.controls['numserie'].value
        this.equipoE.marca = this.formulario.controls['marca'].value
        this.equipoE.modelo = this.formulario.controls['modelo'].value
        this.equipoE.anio = this.formulario.controls['anio'].value
        this.equipoE.descripcion = this.formulario.controls['descripcion'].value
        this.equipoE.ip = this.formulario.controls['ip'].value
        this.equipoE.estado = this.formulario.controls['estado'].value
        this.equipoE.categoria.id = this.formulario.controls['categoria'].value
        this.equipoE.ubicacione.id = this.formulario.controls['ubicacione'].value
        this.equipoE.usuarioe.id = this.user.id
        this.equipoService.editarEquipo(this.equipoE).subscribe(resp => {
          this.showLoading = false
          Swal.fire({
            confirmButtonColor: "#a90000",
            title: 'Éxito',
            text: `Modificado correctamente`,
            icon: 'success',
          });
          this.reload();
          this.formulario.reset();
          this.modalService.dismissAll();

        }, (err) => {
          Swal.fire({
            confirmButtonColor: "#a90000",
            title: 'Error',
            text: `Algo fallo`,
            icon: 'error',
          });
        })

      } else {
        Swal.fire({
          confirmButtonColor: "#a90000",
          position: 'center',
          title: 'Faltan datos en el formulario',
          text: 'completar campos requeridos',
          icon: 'warning',
        });
      }
    } else {
      Swal.fire({
        confirmButtonColor: "#a90000",
        title: 'Error',
        text: 'No se puede cambiar el estado hasta que pasen al menos 4 años desde la compra.',
        icon: 'error',
      });
    }
  }
  realizarModificacion() {
    this.showLoading = false
    this.equipoE.id = this.equipoE.id;
    this.equipoE.numinventario = this.formulario.controls['numinventario'].value;
    this.equipoE.numserie = this.formulario.controls['numserie'].value
    this.equipoE.marca = this.formulario.controls['marca'].value;
    this.equipoE.modelo = this.formulario.controls['modelo'].value;
    this.equipoE.anio = this.formulario.controls['anio'].value;
    this.equipoE.descripcion = this.formulario.controls['descripcion'].value;
    this.equipoE.ip = this.formulario.controls['ip'].value;
    this.equipoE.estado = this.formulario.controls['estado'].value;
    this.equipoE.categoria.id = this.formulario.controls['categoria'].value;
    this.equipoE.ubicacione.id = this.formulario.controls['ubicacione'].value;
    this.equipoE.usuarioe.id = this.user.id;

    this.equipoService.editarEquipo(this.equipoE).subscribe(resp => {
      this.showLoading = false
      Swal.fire({
        confirmButtonColor: "#a90000",
        title: 'Éxito',
        text: `Modificado correctamente`,
        icon: 'success',
      });
      this.reload();
      this.formulario.reset();
      this.modalService.dismissAll();
    }, (err) => {
      Swal.fire({
        confirmButtonColor: "#a90000",
        title: 'Error',
        text: `Algo falló`,
        icon: 'error',
      });
    });
  }
  editarEstado() {
    this.showLoading = false
    // Obtén el año de compra almacenado
    const anioCompra = this.formulario.get('anio').value;
    // Obtén el año actual
    const anioActual = new Date().getFullYear();
    // Calcula cuántos años han transcurrido desde la compra
    const aniosTranscurridos = anioActual - anioCompra;
    const nuevoEstado = this.formulario.controls['estado'].value;

    if (nuevoEstado === 'Bueno' || nuevoEstado === 'Dañado') {
      this.equipoE.id = this.equipoE.id
      this.equipoE.estado = this.formulario.controls['estado'].value
      this.equipoService.editarEquipo(this.equipoE).subscribe(resp => {
        this.showLoading = false
        Swal.fire({
          confirmButtonColor: "#a90000",
          title: 'Éxito',
          text: 'Estado Modificado correctamente',
          icon: 'success',
        });
        this.reload();
        this.formulario.reset();
        this.modalService.dismissAll();
      }, (err) => {
        Swal.fire({
          confirmButtonColor: "#a90000",
          title: 'Error',
          text: `Algo fallo`,
          icon: 'error',
        });
      })


    } else if (aniosTranscurridos >= 4) {
      // Realiza la edición del estado
      this.equipoE.id = this.equipoE.id
      this.equipoE.estado = this.formulario.controls['estado'].value
      this.equipoService.editarEquipo(this.equipoE).subscribe(resp => {

        Swal.fire({
          confirmButtonColor: "#a90000",
          title: 'Éxito',
          text: 'Estado Modificado correctamente',
          icon: 'success',
        });
        this.reload();
        this.formulario.reset();
        this.modalService.dismissAll();
      }, (err) => {
        Swal.fire({
          confirmButtonColor: "#a90000",
          title: 'Error',
          text: `Algo fallo`,
          icon: 'error',
        });
      })


    } else {
      Swal.fire({
        confirmButtonColor: "#a90000",
        title: 'Error',
        text: 'No se puede cambiar el estado hasta que pasen al menos 4 años desde la compra.',
        icon: 'error',
      });
    }


  }






  openModal(content: any) {
    this.modalService.open(content, { size: 'lg', backdrop: 'static', keyboard: false });
  }

  esCampoValido(campo: string) {
    const validarCampo = this.formulario.get(campo);
    return !validarCampo?.valid && validarCampo?.touched
      ? 'is-invalid' : validarCampo?.touched ? 'is-valid' : '';
  }

  reload() {
    this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
      this.equipos = []
      dtInstance.destroy()
      this.cargarDatos()

    });
  }




  openModalPDF(content: any) {
    this.modalService.open(content, { backdrop: 'static', keyboard: false, size: 'sm' });
  }

  //PARA LA AYUDA
  openModalA(content) {
    this.modalService.open(content, {
      keyboard: false,
      scrollable: true, size: 'lg'
    });
  }
  estados(event: any) {
    this.estado = event.target.value;
  }
  imprimir() {
    if (this.estado === "Todos") {
      this.descargarPdfFullEquipo()
    } else {
      this.descargarPdfParametro()
    }
  }
  descargarPdfParametro() {
    this.showLoading = true
    this.equipoService.generarPdfEquipoxEstado(this.estado).subscribe((data: Blob) => {
      this.showLoading = false
      const blob = new Blob([data], { type: 'application/pdf' });
      const link = document.createElement('a');
      let url = URL.createObjectURL(blob);
      window.open(url);
    });
  }
  descargarPdfFullEquipo() {
    this.showLoading = true
    this.equipoService.generarPdfFullEquipo().subscribe((data: Blob) => {
      this.showLoading = false
      const blob = new Blob([data], { type: 'application/pdf' });
      // const link=document.createElement('a');
      let url = URL.createObjectURL(blob);
      window.open(url);
    });
  }

  ngOnDestroy() {
    this.dtTrigger.unsubscribe();
  }




}
